
import Swiper from 'vux/src/components/swiper/swiper.vue'

import stickySlot from '@/view/AuctionPage/stickySlot.vue'
import apiUrl from '@/api/apiUrl'
import httpService from '@/api/httpService'
import { util } from '@/utils/util'
import { Toast } from 'vant';

export default {
name: 'AuctionPage',
components: {
Swiper,
stickySlot
},
created() {

console.log(this.$store)
},
data() {
return {
demo01_list:[{
url: '/introduction',
img: '../../static/img/index_banner.png',
title: ''
},
],
demo01_index: 0,

priceSortList: [{text: "起拍价默认", value: ''},
{text: "起拍价从低到高", value: 'ASC'},
{text: "起拍价从高到低", value: 'DESC'}],
priceSort: '',
timeSortList: [{text: "截止时间默认", value: ''},
{text: "截止时间正序", value: 'DESC'},
{text: "截止时间倒序", value: 'ASC'}],
timeSort: '',
kindSortList: [{text: "标的类别", value: ''},
{text: "手机数码", value: 1},
{text: "生活电器", value: 2},
{text: "护肤/化妆品", value: 3},
{text: "影音图书", value: 4},
{text: "零食", value: 5},
{text: "珠宝首饰", value: 6},
{text: "其他", value: 7}],
kindSort: '',

commodityList: [],
loading: false,
finished: false,
refreshLoading: false,

pageSize: 10,
currentPage: 1,
}
},
methods: {
demo01_onIndexChange (index) {
this.demo01_index = index
},

updateList() {
const opts = {
apiObj: apiUrl.findAllGoods,
query: {
page: this.currentPage - 1,
size: this.pageSize,
priceSort: this.priceSort,
timeSort: this.timeSort,
type: this.kindSort,
}
}
httpService.accessAPI(opts)
.then(res => {
this.commodityList.splice(0);
res.data.forEach((item, index) => {
this.commodityList.push(item)
})
this.commodityList.forEach(item => {
item.endTime = util.dateToStr(new Date(item.endTime), 5)
})
this.commodityList.forEach(item => {
item.imgs = item.imgs.split(";");
console.log(item.imgs[0]);
})

console.log(this.refreshLoading);
console.log(this.loading);

this.refreshLoading = false;


if(this.commodityList.length <= this.currentPage * this.pageSize) {
this.finished = true;
}else {
this.currentPage ++
}
}, rej => {
Toast(res.msg);
this.commodityList = []

this.finished = true;
})
},
onLoad() {
this.updateList()
},
onRefresh() {

console.log(this.finished);
console.log(this.refreshLoading);
this.currentPage = 1
this.commodityList = []
this.updateList();
return;

this.currentPage = 1
this.commodityList = []
const opts = {
apiObj: apiUrl.findAllGoods,
query: {
page: this.currentPage - 1,
size: this.pageSize,
priceSort: this.priceSort,
timeSort: this.timeSort,
type: this.kindSort,
}
}
httpService.accessAPI(opts)
.then(res => {
if(res.code !== '0'){
Toast(res.msg);
return;
}

this.refreshLoading = false;
this.commodityList.splice(0);
res.data.forEach((item, index) => {
this.commodityList.push(item)
})
this.commodityList.forEach(item => {
item.endTime = util.dateToStr(new Date(item.endTime), 5)
})
this.commodityList.forEach(item => {
item.imgs = item.imgs.split(";");
console.log(item.imgs[0]);
})
if(this.commodityList.length <= this.currentPage * this.pageSize) {
this.finished = true;
}else {
this.currentPage ++
this.finished = false
}

})
},
goDetailHandler(index) {

let whileShowBtn = '';
if(this.$store.state.user.id === this.commodityList[index].sellerId){
whileShowBtn = true;
}else{
whileShowBtn = false;
}
this.$store.commit('user/setStatusA', {status: 0, active: 0})
this.$store.commit('user/setgoodsStatus', 1)
this.$router.push({
path: '/detail',
name: 'Detail',
query: {
goodsId: this.commodityList[index].goodsId,
shwoBtn:whileShowBtn,
goodsstatus:1
}
})
},
toTop() {
window.scrollTo(0,0);
}
},
watch: {
priceSort() {
this.timeSort = ''
this.toTop()
this.currentPage = 1
this.updateList()
},
timeSort() {
this.priceSort = ''
this.toTop()
this.currentPage = 1
this.updateList()
},
kindSort() {
this.toTop()
this.currentPage = 1
this.updateList()
},
},
}



